import { ReactLocation, Router } from "@tanstack/react-location"
import NavChat from "./nav/ChatNav";
import ChatIndexPage from "./index/ChatIndexPage";
import ChatClassPage from "./class/ChatClassPage";

const ChatPage = () => {
  const location = new ReactLocation();
  
  return (
    <Router
      location={location}
      routes={[
        { path: "/chat/index", element: <ChatIndexPage /> },
        { path: "/chat/class", element: <ChatClassPage /> },
      ]}>
      <NavChat />
    </Router>
  )
}

export default ChatPage