
import { createAction, createReducer } from '@reduxjs/toolkit';

const INITIAL_STATE = {};

export const userCreate = createAction('USER_CREATE');
export const userLogin = createAction('LOGIN');

export default createReducer(INITIAL_STATE, {
	[userCreate.type]: (state, action) =>  action.payload,
	[userLogin.type]: (state, action) =>  action.payload,
});

