import React from "react";
import {Helmet} from "react-helmet";

const AlterTheme = () => {
  const [languageCss, setLanguageCss] = React.useState('/pt-br_en/dark.css')
  return(
    <>
      <Helmet>
        <link rel="stylesheet" href={`/languages/${languageCss}`} />
      </Helmet>    
    </>
  )
}

export default AlterTheme